@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin: 25px 10px;
}
footer p {
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: #272a31;
}
footer img {
  filter: grayscale(0.9);
  width: 160px;
}

.hero {
  background: url("../img/educationbg.png");
  background-color: #d3d3d3;
}

header {
  position: fixed;
  background-color: white;
  width: 100%;
  top: 0;
  padding: 25px;
  z-index: 10;
}
@media (min-width: 1025px) {
  header {
    padding: 20px 15vw;
  }
  header img {
    filter: grayscale(0.5);
  }
}
header nav .sign-up {
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
  color: #ec5242;
  border: 5px solid #ec5242;
  width: 190px;
}
@media (min-width: 768px) and (max-width: 1400px) {
  header nav .sign-up {
    font-size: 16px;
    width: 150px;
  }
}
header nav svg {
  height: 25px;
  width: 25px;
}
header nav ul,
header nav a {
  gap: 5vw;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #999da5;
  cursor: pointer;
  margin: 0;
  text-decoration: none;
}
@media (min-width: 768px) and (max-width: 1400px) {
  header nav ul,
header nav a {
    font-size: 18px;
    gap: 2.5vw;
  }
}
header nav ul a:hover,
header nav a a:hover {
  color: #ec5242;
}
header nav .shadow-menu {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #ec5242;
  display: flex;
  flex-direction: column;
  padding: 25px 27px;
  visibility: hidden;
}
header nav .shadow-menu svg {
  width: 50px;
  height: 50px;
}
header nav .shadow-menu ul {
  opacity: 1;
  padding: 30px 40px;
  gap: 45px;
}
@media (min-width: 768px) {
  header nav .shadow-menu ul {
    gap: 5px;
  }
}
header nav .shadow-menu ul li a {
  color: white;
  font-size: 32px;
}
header nav .shadow-menu ul li .sign-up {
  color: white;
  border: 4px solid #d3d3d3;
}
header nav .showsmenu {
  visibility: visible;
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

hr {
  margin-top: 24px;
  border: 1px solid #ec5242;
  width: 50%;
  margin-inline: auto;
}

body {
  width: 100%;
}

ul,
li {
  padding: 0;
  list-style: none;
}

@font-face {
  font-family: "COCOGOOSE";
  src: url("../../../dist/assets/fonts/Cocogoose_trial.eot") format("eot"), url("../../../dist/assets/fonts/Cocogoose_trial.woff") format("woff"), url("../../../dist/assets/fonts/Cocogoose_trial.ttf") format("truetype"), url("../../../dist/assets/fonts/Cocogoose_trial.svg#COCOGOOSE") format("svg");
  font-weight: normal;
  font-style: normal;
}
.headline {
  padding: 80px 25px;
  margin-top: 60px;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ec5242;
}
.headline p {
  font-size: 16px;
}
@media (min-width: 768px) {
  .headline p {
    font-size: 2rem;
    line-height: 2rem;
  }
}
.headline h1 {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 34px;
  text-transform: uppercase;
  color: #ec5242;
}
@media (min-width: 768px) {
  .headline h1 {
    font-size: 3rem;
    line-height: 3rem;
  }
}
.headline .headline-box {
  background-color: #f7f7f7;
  border: 1px solid white;
  padding: 20px;
  margin: 20px 0;
}
.headline .headline-box p {
  color: #272a31;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .headline .headline-box p {
    font-size: 2rem;
    line-height: 2rem;
  }
}
.headline .date-event {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  color: #272a31;
  margin: 30px 0;
}
.headline .adress {
  color: #272a31;
}

.partners-logo {
  background-color: #272a31;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  padding: 50px 25px;
  text-align: center;
  color: white;
}
.partners-logo hr {
  width: 15%;
}
.partners-logo .wrap-logos ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  flex-wrap: wrap;
}
.partners-logo img {
  width: 90px;
  margin: 20px 10px;
  filter: grayscale(1);
}
@media (min-width: 768px) {
  .partners-logo img {
    margin: 20px 50px;
    width: 168px;
  }
}

.course {
  background: rgba(0, 0, 0, 0.7);
  background-image: url("../img/bg-courset.png");
  background-repeat: repeat;
  padding: 50px 20px;
  min-height: 40vh;
}
@media (min-width: 768px) {
  .course {
    text-align: center;
  }
}
.course hr {
  width: 15%;
}
.course h2 {
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  color: white;
}
.course p.see-all {
  color: white;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
}
.course ul {
  margin: 25px 0 25px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.course ul li {
  padding: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.3);
  gap: 12px;
}
@media (min-width: 768px) {
  .course ul li {
    height: 305px;
    max-width: 400px;
  }
}
.course ul li h3 {
  color: #ec5242;
}
.course ul li p {
  color: white;
}
.course ul li img {
  width: 38px;
  height: 38px;
  flex-basis: 30%;
}

.speakers {
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  color: #272a31;
  padding: 50px 20px 50px 20px;
}
.speakers h2 {
  text-align: center;
  font-weight: 500;
}
.speakers h3 {
  font-weight: 700;
  font-size: 16px;
}
.speakers hr {
  width: 15%;
}
.speakers ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
@media (min-width: 768px) {
  .speakers ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    padding: 10px 18%;
  }
}
@media (min-width: 600px) and (max-width: 968px) {
  .speakers ul {
    padding: 10px 6%;
  }
}
.speakers ul li {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: 25px;
  padding: 10px;
}
@media (min-width: 768px) {
  .speakers ul li {
    justify-content: flex-start;
  }
}
.speakers ul li img {
  width: 100px;
  height: 100px;
}
.speakers ul li .bg-square {
  position: relative;
}
.speakers ul li .bg-square::after {
  content: url("../img/square-bg.png");
  position: absolute;
  z-index: -1;
  bottom: 12px;
  left: -14px;
  width: 90%;
  height: 90%;
  background: linear-gradient(red, firebrick);
}
.speakers ul li .description p {
  font-size: 12px;
  margin: 0;
  line-height: 15px;
}
.speakers ul li .description hr {
  width: 10%;
  margin-inline: 0;
  margin: 0;
  color: #d3d3d3;
}
.speakers ul li .description p:first-of-type {
  color: #ec5242;
  font-size: 12px;
  margin: 5px 0;
  font-style: italic;
}

@media (min-width: 1024px) {
  .pr1 {
    padding-right: 1em;
  }

  .pr2 {
    padding-right: 2em;
  }

  .pr3 {
    padding-right: 3em;
  }

  .pr4 {
    padding-right: 4em;
  }

  .pr5 {
    padding-right: 5em;
  }

  .pr6 {
    padding-right: 6em;
  }

  .pr7 {
    padding-right: 7em;
  }

  .pr8 {
    padding-right: 8em;
  }

  .pr9 {
    padding-right: 9em;
  }

  .pl1 {
    padding-left: 1em;
  }

  .pl2 {
    padding-left: 2em;
  }

  .pl3 {
    padding-left: 3em;
  }

  .pl4 {
    padding-left: 4em;
  }

  .pl5 {
    padding-left: 5em;
  }

  .pl6 {
    padding-left: 6em;
  }

  .pl7 {
    padding-left: 7em;
  }

  .pl8 {
    padding-left: 8em;
  }

  .pl9 {
    padding-left: 9em;
  }
}